import {
  AfterViewInit,
  Component,
  Input,
  OnInit,
  ViewChild,
} from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { Campaign } from '../../../../interfaces/voicebot/campaign.model';

import { Gestion } from '../../../../interfaces/voicebot/gestion.model';
import { Modal } from '../../../../interfaces/utils/Modal';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { TableColumn } from '../../../../interfaces/utils/Table-column';
import {
  BotResponse,
  ccsStyleByTypeField,
  getNameTypeField,
  OrderResponse,
} from '../../../../interfaces/voicebot/bot-response';
import { UserData } from '../../../../interfaces/utils/User-data';

import icEdit from '@iconify/icons-ic/twotone-edit';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icSearch from '@iconify/icons-ic/twotone-search';
import icAdd from '@iconify/icons-ic/twotone-add';
import icFilterList from '@iconify/icons-ic/twotone-filter-list';
import { SelectionModel } from '@angular/cdk/collections';
import icMoreHoriz from '@iconify/icons-ic/twotone-more-horiz';
import icFolder from '@iconify/icons-ic/twotone-folder';
import { fadeInUp400ms } from '../../../../../@vex/animations/fade-in-up.animation';

import {
  MAT_FORM_FIELD_DEFAULT_OPTIONS,
  MatFormFieldDefaultOptions,
} from '@angular/material/form-field';
import { stagger40ms } from '../../../../../@vex/animations/stagger.animation';
import { FormControl } from '@angular/forms';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

import icPlus from '@iconify/icons-ic/baseline-plus';
import icRefresh from '@iconify/icons-ic/baseline-refresh';
import icHeadset from '@iconify/icons-ic/twotone-headset';
import icLoad from '@iconify/icons-ic/twotone-sync';
import { UtilService } from 'src/app/services/utils/util/util.service';
import { Router } from '@angular/router';
import { ProductService } from 'src/app/services/voicebot/bot/bot.service';
import {
  FieldTypeVoicebot,
  FormProduct,
} from 'src/app/interfaces/voicebot/field-type-voicebot';
import { WhareService } from 'src/app/services/wharehouse/wharehouse.service';

@UntilDestroy()
@Component({
  selector: 'list-bot-table',
  templateUrl: './list-order.component.html',
  styleUrls: ['./list-order.component.scss'],
  animations: [fadeInUp400ms, stagger40ms],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class ListOrderComponent implements OnInit, AfterViewInit {
  layoutCtrl = new FormControl('fullwidth');

  subject$: ReplaySubject<BotResponse[]> = new ReplaySubject<BotResponse[]>(1);
  data$: Observable<BotResponse[]> = this.subject$.asObservable();
  Campaigns: BotResponse[];
  Campa√±asArchivada: BotResponse[];
  Gestiones: Gestion[];
  modal: Modal;

  userData: UserData;

  loading = true;
  loadingTabla = true;

  @Input()
  columns: TableColumn<OrderResponse>[] = [
    {
      label: 'Name',
      property: 'name',
      type: 'text',
      visible: true,
      cssClasses: ['font-bold', 'text-secondary'],
    },
    {
      label: 'Number',
      property: 'number',
      type: 'text',
      visible: true,
      cssClasses: ['font-medium', 'text-secondary'],
    },
    {
      label: 'Detail',
      property: 'detail',
      type: 'badge',
      visible: true,
      cssClasses: ['font-medium', 'text-secondary'],
    },
    { label: 'Actions', property: 'actions', type: 'button', visible: true },
  ];
  pageSize = 10;
  pageSizeOptions: number[] = [10, 15, 20, 25];

  dataSource: MatTableDataSource<BotResponse> | null;
  selection = new SelectionModel<BotResponse>(true, []);

  searchCtrl = new FormControl();

  campaign: Campaign;

  icFilterList = icFilterList;
  //Iconos
  icEdit = icEdit;
  icSearch = icSearch;
  icDelete = icDelete;
  icAdd = icAdd;
  icMoreHoriz = icMoreHoriz;
  icHeadset = icHeadset;
  icLoad = icLoad;
  icPlus = icPlus;
  icRefresh = icRefresh;
  products: BotResponse[] = [];

  //Final de iconos

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  //paginatitor archivada
  @ViewChild('categoryPaginator', { read: MatPaginator })
  categoryPaginator: MatPaginator;

  constructor(
    private botService: ProductService,
    private router: Router,
    private whareHouseService: WhareService
  ) {
    const idWhareHouse = this.whareHouseService.selectedWharehouse$.getValue();
    this.botService
      .listProductsByWhareHouse(idWhareHouse.id)
      .subscribe((response) => {
        this.products = response;
      });
  }

  get visibleColumns() {
    return this.columns
      .filter((column) => column.visible)
      .map((column) => column.property);
  }

  ngOnInit() {
    this.dataSource = new MatTableDataSource();
    this.getData();
  }

  getData() {
    let clase = this;
    this.Campaigns = [];
    this.loading = true;
    const idWhareHouse = this.whareHouseService.selectedWharehouse$.getValue();
    this.botService
      .listOrderByWhareHouse(idWhareHouse.id)
      .subscribe((response) => {
        clase.dataSource.data = response;
        this.loading = false;
        clase.loadingTabla = false;
        this.searchCtrl.valueChanges
          .pipe(untilDestroyed(this))
          .subscribe((value) => this.onFilterChange(value));
      });
  }

  refreshDatabutton() {
    //TIENR QUE Refrescar
    //alert('Refrescar');
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  removeItemFromDatasoucre(dataSource: any, index: number) {
    const paginator = dataSource.paginatitor;
    dataSource.data.splice(index, 1);
    dataSource.paginator = paginator;
  }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected()
      ? this.selection.clear()
      : this.dataSource.data.forEach((row) => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  goToNew() {
    const url = `/system/order/new`;
    this.router.navigate([url]);
  }

  // editBot(product: BotResponse) {
  //   const url = `/voicebot/order/${product.id}`;
  //   this.router.navigate([url], { state: product });
  // }

  getProductName(id: number) {
    return this.products.find((p) => p.id === id).name;
  }
}
